.sign-up-container {
    font-family: var(--font-family);
    padding: 8em 0;
    display: flex;
    align-items: center;
    justify-content: center;
}
.sign-up-container .success-cont{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.success-sign {
    padding-left: 10px;
    font-size: 1.5em;
}

.sign-up-container h2 {
    margin: 30px 0;
    font-size: 2em;
    font-weight: bold;
}

.sign_up-btn {
    padding: 2em 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}