.services-body {
    font-family: var(--font-family);
    padding: 7em 4em;
}
.services-main {
  position: relative;
  border-radius: 10px;
  padding: 6em 5em;
  overflow: hidden;
  margin-bottom: 2.5em;
}

.services-main::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(rgba(30,30,30,0.7),rgba(30,30,30,0.7)), url('../../assets/HeadPic.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: right center;
  filter: blur(4px); 
  z-index: -1;
  opacity: 0.8; 
}
.services-main h1 {
    font-size: 3.5em;
    color: var(--color-sec);
}
.services-main p {
    font-size: 1.2em;
    padding-bottom: 2em;
    color: #fff;
}
.service-title1 {
    padding: 4em 0;
    display: flex;
    gap: 20px;
    align-items: center;
}
.service-title2 {
    padding: 4em 0;
    display: flex;
    gap: 20px;
    align-items: center;
}
.service-title-content1 {
    text-align: right;
    color: var(--color-txt);
    flex: 1;
}
.service-title-content1 h2{
    font-size: 2em;
    padding-bottom: 1em;
}
.service-title-content2 h2{
    font-size: 2em;
    padding-bottom: 1em;
}
.service-title-content2 {
    text-align: left;
    color: var(--color-txt);
    flex: 1;
}
.service-title1 img{
    width: 300px;
    height: 230px;
    object-fit: cover;
    border-radius: 20px;
}
.service-title2 img{
    width: 300px;
    height: 230px;
    object-fit: cover;
    border-radius: 20px;
}
.container-service-m {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #05DEE64A;
    padding: 2em;
    border-radius: 20px;
    margin: 4em 0;
}
.card-container-service {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: justify;
}
.card-container-service p {
    padding: 1em 0;
}
.card-container-all {
    display: flex;
    gap: 40px;
    margin-top: 2em;
}
.testimonials-con {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: var(--color-txt);
    padding-top: 4em;
}
.container-content-tes {
    display: flex;
    flex-direction: row;
    
}

@media screen and (max-width: 768px) {
    .services-main h1 {
        font-size: 3em;
        color: var(--color-sec);
    }
    .card-container-all {
        flex-direction: column;
    }
    .service-title1 {
        flex-direction: column-reverse;
    }
    .service-title2 {
        flex-direction: column;
    }
    .service-title-content1 {
        text-align: justify;
    }
    .service-title-content2 {
        text-align: justify;
    }
    .service-title-content1 h2{
        text-align: center;
    }
    .service-title-content2 h2{
        text-align: center;
    }
    .container-service-m h1, h4 {
        text-align: center;
    }
}

@media screen and (max-width: 450px) {

    .services-main::before {
        background-image: none;
    }
    .services-body {
        padding: 5em 2em;
    }
    .services-main {
        padding: 2em 0;
    }
    .services-main h1 {
        font-size: 2em;
    }
    .services-main p {
        font-size: 1em;
        color: var(--color-txt);
    }
}
