.HeadPrivacy{
    color: #fff;
    padding-bottom: 2em;
    align-self: flex-start;
}

.PP_padding {
    display: flex;
    align-items: center;
    flex-direction: column;
    font-family: var(--font-family);
    padding: 8em 10em;
}
.PP_Container{
    padding: 2em 4em 2em 3em;
    background-color: #fff;
    border-radius: 20px;
}

.PP_content{
    color: #000000;
    padding-left: 2em;
}
.PP_content span{
    font-weight: bolder;
    font-size:22px;
}
.PP_content ul{
    padding-left: 25px;
}
@media screen and (max-width: 768px) {
    .HeadPrivacy {
        margin-left: 0;
    }

    .PP_padding {
        padding: 5em 1em; 
    }

    .PP_Container {
        padding: 1em; 
    }

    .PP_content {
        padding-left: 1em; 
    }
}
@media screen and (max-width: 425px) {
    .HeadPrivacy {
        padding-bottom: 1em; 
    }

    .PP_padding {
        padding: 5em 1em; 
    }

    .PP_Container {
        padding: 0.5em; 
    }

    .PP_content {
        padding-left: 0.5em; 
    }
}
@media screen and (max-width: 1024px) {
    
    .HeadPrivacy {
      margin-left: 20px; 
    }
  
    .PP_padding {
      padding: 4em 2em; 
    }
  
    .PP_Container {
      padding: 1em 2em; 
    }
  
    .PP_content {
      padding-left: 1em;
    }
  }