.faq-container {
    font-family: var(--font-family);
    color: #fff;
}
.faq-card-container {
    display: flex;
    gap: 40px;
}
.faq-card > * {
    padding: 0.5em 0;
}
.faq-card p {
    text-align: justify;
}
@media screen and (max-width: 768px) {
    
}
@media screen and (max-width: 425px) {
    .faq-card-container {
        flex-wrap: wrap;
    }
}