@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

:root {
    --font-family: 'Poppins', sans-serif;
    --gradient-txt: radial-gradient(circle at 0% 0%, rgba(88, 213, 203, 1) 0%, rgba(10, 140, 80, 1) 100%);

    --color-bg: #fffdfa;
    --color-pri: #58D5CB;
    --color-sec: #05DEE6;
    --color-acc: #0a8c50;
    --color-footer: #1A202C;
    --color-btn: #0a8c50;
    --color-txt: #000;
    --color-sec-txt: #A0ABC0;
    --color-cont:#F7F8F9;
    --color-lowblack:#323232;
}

* {
    font-family: var(--font-family);
}