.contact-info{
    font-family: var(--font-family);
    padding: 5em 10em;
    padding-bottom: 14em;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.phone, .message, .email{
    margin: 4em 0;
    display: flex;
    align-items: center;
    flex-basis: 200px;
    gap: 2em;
    color: var(--color-contact);
    padding: 0 4em;
}
.phone-content h1, .message-content h1, .email-content h1{
    padding: 0.5em 0;
}   
.phone-content p, .message-content p, .email-content p{
    padding: 1em 0;
}  

.message {
    text-align: right;
}

@media screen and (max-width: 786px) {
    .contact-info{
        padding: 4em 2em;
    }
}

@media screen and (max-width: 425px) {
    .contact-info{
        padding: 4em 2em;
    }
    .phone, .message, .email{
        text-align: left;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        margin-bottom: 6em;
        padding: 0 0;
    }
    .message {
        flex-direction: column-reverse;
    }
}