.body-content {
    font-family: var(--font-family);
    padding: 6.5em 0;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
}

.finish-form-text {
    display: flex;
    align-items: center;
    justify-content: center;
    white-space: nowrap;
}
.finish-form img {
    width: 60%;
    margin: 0.5em 0;
    height: auto;
}

.finish-form {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: #fff;
    padding: 2em 4em;
    margin: 2em 0;
    border-radius: 10px;
}



