.swiper {
  width: 100%;
  height: 300px;
  color: var(--color-txt);
  padding: 2em 4em;
  overflow: hidden;
  position: relative;
  text-align: justify;
  border-radius: 10px;
}
.swiper p {
  font-size: 1.2em;
}
.swiper h4 {
    font-size: 1.3em;
    float: right;
}

@media (max-width: 768px) {
  .swiper {
    height: 400px;
  }
}
@media (max-width: 425px) {
  .swiper {
    padding: 2em 2em;
    height: 400px;
  }
  .swiper p{
    font-size: 0.8em;
  }
  .swiper h4{
    font-size: 0.9em;
  }
}
