.body___app {
    display: flex;
    margin: 8em 0;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.tlogo-img {
    width: 100%;
    max-width: 200px;
    margin-bottom: 2em;
}
.login-container {
    font-family: var(--font-family);
    display: flex;
    align-items: center;
    justify-content: center;
}
.login-container h2 {
    margin: 30px 0;
    font-size: 2em;
    font-weight: bold;
}
.login-container p {
    display: flex;
    align-items: center;
    padding: 10px 0.5em;
    width: 100%;
    text-align: left;
}
.error{
    color: rgb(179, 0, 0);
}
.success{
    padding-left: 10px;
    color: var(--color-acc);
}
.group-feature {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1em;
    flex-wrap: wrap;
}

@media (max-width: 1280px) {
    .body___app {
        margin: 3em 0;
    }
}

@media (max-width: 450px) {
    .body___app {
        margin: 5em 0;
    }
    .login-container {
        box-shadow: none;
    }
    .tlogo-img {
        margin-bottom: 0;
    }
}

@media (max-width: 450px) {
    .body___app {
        margin: 5em 0;
    }
    .login-container {
        box-shadow: none;
    }
    .tlogo-img {
        margin-bottom: 0;
    }
}