.account-page-content {
  padding: 6em 4em;
}

.edit-profile-form {
    margin: 2em 0;
    padding: 2em;
    color: var( --color-cont);
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    display: flex;
}
.tracking-container {
    margin: 2em 0;
    padding: 2em;
    background: #fff;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
}
.tracking-form {
    margin: 1em 0;
    padding: 2em;
    background: #d2d2d2;
    border-radius: 10px;
    text-align: center;
    font-family: var(--font-family);
    font-size: 2em;
}
.search-form {
    display: flex;
    justify-content: flex-end;
}
.save-btn {
    display: flex;
    justify-content: flex-end;
    padding-bottom: 3em;
}
.next-btn {
    display: flex;
    justify-content: flex-end;
}

@media screen and (max-width: 768px) {
  .account-page-content {
    padding: 6em 2em;
  }
  .edit-profile-form {
    flex-direction: column-reverse;
  }
  .tracking-form h1{
    font-size: 2em;
  }
}
@media screen and (max-width: 425px) {
  .account-page-content {
    padding: 6em 1.5em;
  }
  .tracking-form h1{
    font-size: 0.8em;
  }
}
