.bk-btn {
    font-family: var(--font-family);
    background: #e0e0e0;
    border: none;
    border-radius: 10px;
    width: 220px;
    height: 60px;
    font-size: 1.5rem;
    font-weight: bold;
    color: var(--color-txt);
    transition: ease 0.6s;
}

.bk-btn:hover {
    cursor: pointer;
    border: none;
    background: var(--color-btn);
    color: #fff;
    -webkit-transform: translateZ(10px) translateY(-5px);
    transform: translateZ(10px) translateY(-5px);
    -webkit-box-shadow: 0px 14px 31px -12px rgba(149,179,168,1), inset 0 -6px 14px -6px rgba(104, 104, 104, 0.5);
    -moz-box-shadow: 0px 14px 31px -12px rgba(149,179,168,1);
    box-shadow: 0px 14px 31px -12px rgba(149,179,168,1), inset 0 -6px 14px -6px rgba(104, 104, 104, 0.5);
}