.logo-border {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 5rem;
    background: linear-gradient(rgba(235, 235, 235, 0.904),rgba(214, 214, 214, 0.959)),url(../../assets/map-bg.jpg);
}
.marq {
    color: var(--color-bg);
    padding: 1em 0;
    background: var(--color-acc);
    font-size: 1.2em;
}

.logo-img {
    margin-bottom: 3.5em;
    background-color: var(--color-pri);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 30px;
    width: calc(5 * 100px);

    /* shadow animation */
    -webkit-animation: shadow-drop-center 5s cubic-bezier(0.250, 0.460, 0.450, 0.940) 0.4s both, slide-in-blurred-top 0.4s cubic-bezier(0.230, 1.000, 0.320, 1.000) both;
	animation: shadow-drop-center 5s cubic-bezier(0.250, 0.460, 0.450, 0.940) 0.4s both, slide-in-blurred-top 1s cubic-bezier(0.230, 1.000, 0.320, 1.000) both;
}


/* shadow key anim */
@-webkit-keyframes shadow-drop-center {
    0% {
      -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
              box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    }
    100% {
        -webkit-box-shadow: 0px 0px 59px 1px rgba(147,177,166,1);
        -moz-box-shadow: 0px 0px 59px 1px rgba(147,177,166,1);
        box-shadow: 0px 0px 59px 1px rgba(147,177,166,1);
    }
  }
  @keyframes shadow-drop-center {
    0% {
      -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
              box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    }
    100% {
        -webkit-box-shadow: 0px 0px 59px -5px rgba(147,177,166,1);
        -moz-box-shadow: 0px 0px 59px -5px rgba(147,177,166,1);
        box-shadow: 0px 0px 59px -5px rgba(147,177,166,1);
    }
  }


  @-webkit-keyframes slide-in-blurred-top {
    0% {
      -webkit-transform: translateY(1000px) scaleY(2.5) scaleX(0.2);
              transform: translateY(1000px) scaleY(2.5) scaleX(0.2);
      -webkit-transform-origin: 50% 0%;
              transform-origin: 50% 0%;
      -webkit-filter: blur(40px);
              filter: blur(40px);
      opacity: 0;
    }
    100% {
      -webkit-transform: translateY(0) scaleY(1) scaleX(1);
              transform: translateY(0) scaleY(1) scaleX(1);
      -webkit-transform-origin: 50% 50%;
              transform-origin: 50% 50%;
      -webkit-filter: blur(0);
              filter: blur(0);
      opacity: 1;
    }
  }
  @keyframes slide-in-blurred-top {
    0% {
      -webkit-transform: translateY(1000px) scaleY(2.5) scaleX(0.2);
              transform: translateY(1000px) scaleY(2.5) scaleX(0.2);
      -webkit-transform-origin: 50% 0%;
              transform-origin: 50% 0%;
      -webkit-filter: blur(40px);
              filter: blur(40px);
      opacity: 0;
    }
    100% {
      -webkit-transform: translateY(0) scaleY(1) scaleX(1);
              transform: translateY(0) scaleY(1) scaleX(1);
      -webkit-transform-origin: 50% 50%;
              transform-origin: 50% 50%;
      -webkit-filter: blur(0);
              filter: blur(0);
      opacity: 1;
    }
  }


.logo-img img {
    height: 130px;
}

.head-content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.h1_txt {
    font-family: var(--font-family);
    color: var(--color-acc);
    text-align: center;
    font-weight: bolder;
    font-size: 4.5rem;
    padding: 1.5em 0;
}

.card-container {
    font-family: var(--font-family);
    padding: 6rem 1rem;
    display: flex;
    gap: 40px;
}

.card-head {
    width: calc(5 * 62px);
    display: flex;
}

.card-title {
    color: var(--color-txt);
    padding-right: 0.5rem;
}

.card-details h2{
    font-size: 1.1rem;
    color: var(--color-txt);
    padding-bottom: 1rem;
}

.card-details p{
    font-size: 0.9rem;
    color: var(--color-txt);
    text-align: justify;
}

.head-content_btn {
    padding-top: 5rem;
    padding-bottom: 5rem;
}

.content-details {
    display: flex;
    background: radial-gradient(circle at 48% 51%, rgba(0, 188, 212, 0.36) 0%, rgba(255, 253, 250, 1) 41%, rgba(255, 253, 250, 1) 100%);
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 5em 10em;
    font-family: var(--font-family);
}
.content-details h1 {
    font-size: 2.8em;
    padding-bottom: 1em;
    color: var(--color-txt);
}
.content-details p {
    font-size: 1.3em;
    text-align: center;
    color: var(--color-txt);
}
.content-details img {
    margin: 4em 0;
    width: 250px;
    height: auto;
    transition: ease 0.4s;
}
.content-details img:hover {
    transform: scale(1.5);
    transition: ease 0.4s;
}
@media screen and (max-width: 1280px) {
    .card-container {
        flex-direction: column;
    }
    .h1_txt {
        font-size: 3.2rem;
    }
}
@media screen and (max-width: 769px) {
    .logo-img img {
        height: 75px;
    }
    .h1_txt {
        padding-bottom: 1em;
    }
    .logo-border {
        padding-top: 8em;
    }
    .logo-img {
        margin-top: 5%;
        margin-bottom: 2em;
        width: calc(3 * 95px);
    }

    .h1_txt {
        font-size: 2.4rem;
    }

    .card-head {
        width: calc(5 * 50px);
    }
    .content-details {
        padding: 5em 1em;
    }
    .marq {
        padding: 1em 0;
        font-size: 1em;
    }
}

@media screen and (max-width: 425px) {
    .h1_txt {
        text-align: left;
        font-size: 1.8rem;
    }
    .content-details img {
        margin: 4em 0;
        width: 150px;
        transition: ease 0.4s;
    }
    .content-details h1 {
        font-size: 1.5em;
        text-align: center;
    }
    .content-details p {
        font-size: 1em;
        text-align: justify;
    }
    .content-details img:hover {
        transform: scale(1.4);
        transition: ease 0.4s;
    }
    .marq {
        padding: 1em 0;
        font-size: 0.8em;
    }
}