.footer-section {
    font-family: var(--font-family);
    box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
    background: radial-gradient(circle at 0% 0%, rgba(88, 213, 175, 1) 0%, rgba(10, 140, 80, 1) 67%);
    gap: 30px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    color: var(--color-txt);
    font-size: 0.8rem;
    padding: 4rem 4rem;
    text-align: center;
}
.footer-logo img {
    height: 40px;
}
.footer-download img {
    height:28px;
}
.footer-logo_company {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.footer-logo_company > * {
    padding: 0.8rem 0;
}
.footer-nav_links {
    display: flex;
    white-space: nowrap;
}
.footer-nav_links a{
    padding: 1rem 1rem;
    transition: ease 0.6s;
}
.footer-nav_links a:hover{
    color: var(--color-pri);
}
.footer-media {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    white-space: nowrap;
}
.footer-media_item {
    margin-bottom: 2rem;
}
.footer-media > * {
    padding: 0.5rem 0;
}
.footer-media_item a{
    padding: 0 1rem;
    transition: ease 0.6s;
}
.footer-media_item a:hover{
    color: var(--color-pri);
}
.footer-download a {
    padding: 0 1rem;
}
.footer-details p {
    padding: 0.5em 0;
}