.dashboard-container {
  display: flex;
}

.form-section {
  flex: 1;
  margin-top: 6em;
  margin-bottom: 2em;
  margin-left: 12em;
  margin-right: 2em;
  box-sizing: border-box;
  padding: 2em;
  background: #ececec;
  border-radius: 10px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
}
@media (max-width: 768px) {
  .form-section {
    margin-left: 6em;
  }
}
